import React, { Component } from "react";
import "./home.css";

class Home extends Component {
  state = {
    count: 0,
  };
  render() {
    return (
      <div>
        <header className="home-header">
          <h1>{this.state.count}</h1>
          <button onClick={this.increment} className="btn btn-secondary p-2">
            Increment
          </button>
        </header>
      </div>
    );
  }

  increment = () => {
    this.setState({ count: ++this.state.count });
  };
}

export default Home;
