import React, { Component } from "react";
import "./signup.css";
import axios from "axios";

let emailIsInvalid = true;
let nameIsEmpty = true;
let usernameIsEmpty = true;
let passwordIsEmpty = true;
let codeIsEmpty = true;

class Signup extends Component {
  state = {
    email: null,
    name: null,
    username: null,
    password: null,
    disableButton: true,
    disableEnter: true,
    emailError: "",
    usernameError: "",
    passwordError: "",
    verification: false,
    code: "",
    verificationError: "",
  };
  handleEmailChange = (e) => {
    this.setState({ email: e.target.value });
    emailIsInvalid = e.target.value === "" ? true : false;
    this.setState({
      disableButton:
        emailIsInvalid || nameIsEmpty || usernameIsEmpty || passwordIsEmpty,
    });
  };
  handleNameChange = (e) => {
    this.setState({ name: e.target.value });
    nameIsEmpty = e.target.value === "" ? true : false;
    this.setState({
      disableButton:
        emailIsInvalid || nameIsEmpty || usernameIsEmpty || passwordIsEmpty,
    });
  };
  handleUsernameChange = (e) => {
    this.setState({ username: e.target.value });
    usernameIsEmpty = e.target.value === "" ? true : false;
    this.setState({
      disableButton:
        emailIsInvalid || nameIsEmpty || usernameIsEmpty || passwordIsEmpty,
    });
  };
  handlePasswordChange = (e) => {
    this.setState({ password: e.target.value });
    passwordIsEmpty = e.target.value === "" ? true : false;
    this.setState({
      disableButton:
        emailIsInvalid || nameIsEmpty || usernameIsEmpty || passwordIsEmpty,
    });
  };
  handleCodeChange = (e) => {
    this.setState({ code: e.target.value });
    codeIsEmpty = e.target.value.length != 6 ? true : false;
    console.log(codeIsEmpty);
    this.setState({ disableEnter: codeIsEmpty });
  };
  signup = () => {
    // First clear all errors
    this.setState({ emailError: "", usernameError: "", passwordError: "" });

    // Check if all fields are viable
    if (!this.state.email.includes("@")) {
      this.setState({
        emailError: "Please enter a valid email",
      });
      return;
    }
    if (this.state.username.length <= 3) {
      this.setState({
        usernameError: "Usernames must be greater than 3 characters in length",
      });
    }
    if (this.state.password.length <= 7) {
      this.setState({
        passwordError: "Passwords must be greater than 7 characters in length",
      });
    }

    // Send a request to make new user to backend
    const body = {
      email: this.state.email,
      name: this.state.name,
      username: this.state.username.toLowerCase(),
      password: this.state.password,
    };
    axios
      .post("http://localhost:3000/new-user", body)
      .then((res) => this.setState({ verification: true }))
      .catch((error) => {
        let error_status = error.response.status;
        // Handle bad request
        if (error_status == 400) {
        }
        // Handle conflict
        else if (error_status == 409) {
          if (error.response.data == "Email Already Exists") {
            this.setState({
              emailError: "Email already exists. Try logging in instead",
              passwordError: "",
              usernameError: "",
            });
          } else if (error.response.data == "Username Already Exists") {
            this.setState({
              usernameError:
                "Username already exists. Please try another username",
            });
          }
        }
      });
  };
  verify = () => {
    // Send a verification request to the backend
    const body = {
      email: this.state.email,
      code: this.state.code,
    };
    axios
      .post("http://localhost:3000/verify", body)
      .then((res) => console.log("success!"))
      .catch((error) => {
        let error_status = error.response.status;
        // Handle bad request
        if (error_status == 400) {
        }
        // Handle conflict
        if (error_status == 404) {
          this.setState({ verificationError: "Please try again" });
        }
        if (error_status == 401) {
          this.setState({ verificationError: "Wrong Code. Try again" });
        }
      });
  };
  render() {
    // Render the signup page
    if (this.state.verification == false) {
      return (
        <div className="Login-header">
          <div className="card text-center" style={{ width: 300 }}>
            <div className="card-body">
              <div className="card-title">
                <span className="Custom-font">Jennie's Feet</span>
              </div>
              <h6 className="card-subtitle mb-2 text-muted">
                Sign up to see photos and videos from your friends.
              </h6>
              <form>
                <div className="form-group">
                  <input
                    type="email"
                    className="form-control"
                    id="exampleInputEmail1"
                    aria-describedby="emailHelp"
                    placeholder="Enter email"
                    onChange={this.handleEmailChange}
                  ></input>
                  {this.state.emailError && (
                    <small className="text-danger">
                      {this.state.emailError}
                    </small>
                  )}
                </div>
                <div className="form-group">
                  <input
                    className="form-control"
                    placeholder="Full Name"
                    onChange={this.handleNameChange}
                  ></input>
                </div>
                <div className="form-group">
                  <input
                    className="form-control"
                    placeholder="Username"
                    onChange={this.handleUsernameChange}
                  ></input>
                  {this.state.usernameError && (
                    <small className="text-danger">
                      {this.state.usernameError}
                    </small>
                  )}
                </div>
                <div className="form-group">
                  <input
                    type="password"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="Password"
                    onChange={this.handlePasswordChange}
                  ></input>
                  {this.state.passwordError && (
                    <small className="text-danger">
                      {this.state.passwordError}
                    </small>
                  )}
                </div>
                <button
                  className="btn btn-primary custom"
                  type="button"
                  disabled={this.state.disableButton}
                  onClick={this.signup}
                >
                  Sign Up
                </button>
              </form>
            </div>
          </div>
          <div
            className="card text-center"
            style={{ width: 300, marginTop: 10 }}
          >
            <div className="card-body">
              <a>Have an account? </a>
              <a href="/login">Log in</a>
            </div>
          </div>
        </div>
      );
      // Render verification page
    } else {
      return (
        <div className="Login-header">
          <div className="card text-center" style={{ width: 300 }}>
            <div className="card-body">
              <div className="card-title">
                <span className="Custom-font">Jennie's Feet</span>
              </div>
              <h6 className="card-subtitle mb-2">Enter Confirmation Code</h6>
              <a className="card-subtitle mb-2 text-muted">
                Enter the confirmation code we sent to {this.state.email}
              </a>
              <form>
                <br></br>
                <div className="form-group">
                  <input
                    className="form-control"
                    placeholder="Confirmation Code"
                    onChange={this.handleCodeChange}
                  ></input>
                  {this.state.verificationError && (
                    <small className="text-danger">
                      {this.state.verificationError}
                    </small>
                  )}
                </div>
                <button
                  className="btn btn-primary custom"
                  type="button"
                  disabled={this.state.disableEnter}
                  onClick={this.verify}
                >
                  Enter
                </button>
              </form>
            </div>
          </div>
          <div
            className="card text-center"
            style={{ width: 300, marginTop: 10 }}
          >
            <div className="card-body">
              <a>Have an account? </a>
              <a href="/login">Log in</a>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default Signup;
