import React, { Component } from "react";
import "./navbar.css";
import logo from "../../logo.png";

class Navbar extends Component {
  render() {
    let login_elements = (
      <React.Fragment>
        <div style={{ flex: 0.28 }}></div>
        <form className="form-inline" style={{ width: "15%" }}>
          <i className="fas fa-search" aria-hidden="true"></i>
          <input
            className="form-control form-control-sm ml-3 w-75 text-center"
            type="text"
            placeholder="Search"
            aria-label="Search"
          ></input>
        </form>
        <div style={{ flex: 0.28 }}></div>
        <ul className="navbar-nav">
          <a
            className="btn btn-primary"
            href="/login"
            role="button"
            onClick={this.updateScreen}
          >
            Log In
          </a>
          <a className="btn btn-link" href="/signup" role="button">
            Sign Up
          </a>
        </ul>
      </React.Fragment>
    );
    return (
      <div className="Navbar-header">
        <nav className="navbar navbar-expand-lg navbar-light bg-white container-fluid">
          <div style={{ flex: 0.2 }}></div>

          <a className="navbar-brand" href="/">
            <img src={logo} width={50} height={50}></img>
            <span className="Custom-font">Jennie's Feet</span>
          </a>
          {login_elements}
        </nav>
      </div>
    );
  }
}

export default Navbar;
