import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import "./login.css";
import axios from "axios";

let usernameIsEmpty = true;
let passwordIsEmpty = true;

class Login extends Component {
  state = {
    username: null,
    password: null,
    disableButton: true,
    errorMsg: "",
  };
  handleUsernameChange = (e) => {
    this.setState({ username: e.target.value });
    usernameIsEmpty = e.target.value === "" ? true : false;
    this.setState({ disableButton: usernameIsEmpty || passwordIsEmpty });
  };
  handlePasswordChange = (e) => {
    this.setState({ password: e.target.value });
    passwordIsEmpty = e.target.value === "" ? true : false;
    this.setState({ disableButton: usernameIsEmpty || passwordIsEmpty });
  };
  onSubmit = () => {
    const body = {
      username: this.state.username.toLowerCase(),
      password: this.state.password,
    };
    axios
      .post("http://localhost:3000/login", body)
      .then((res) => {
        this.props.history.push("/");
      })
      .catch((error) => {
        let error_status = error.response.status;
        if (error_status === 404) {
          this.setState({ errorMsg: "Incorrect username or password" });
        }
      });
  };
  render() {
    return (
      <div className="Login-header">
        <div className="card text-center" style={{ width: 250 }}>
          <div className="card-body">
            <div className="card-title">
              <span className="Custom-font">Jennie's Feet</span>
            </div>
            <form>
              <div className="form-group">
                <input
                  className="form-control"
                  id="exampleInputEmail1"
                  placeholder="Username"
                  onChange={this.handleUsernameChange}
                ></input>
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Password"
                  onChange={this.handlePasswordChange}
                ></input>
              </div>
              <div>
                <button
                  type="button"
                  className="btn btn-primary custom"
                  disabled={this.state.disableButton}
                  onClick={this.onSubmit}
                >
                  Log In
                </button>
              </div>
              {this.state.errorMsg && (
                <small className="text-danger">{this.state.errorMsg}</small>
              )}
            </form>
            <a href="#">
              <br></br>Forgot password?
            </a>
          </div>
        </div>
        <div className="card text-center" style={{ width: 250, marginTop: 10 }}>
          <div className="card-body">
            <a>Don't have an account? </a>
            <a href="/signup">Sign up</a>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
